.notification {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 300px;
  word-wrap: break-word;
  padding: 20px;
  background-color: var(--notification-bg-color);
  color: var(--notification-text-color);
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.5s;
  z-index: 200;
  font-weight: 700;
}

.notification.show {
  opacity: 1;
}
